import React, { Component } from 'react';
import requester from '../../../utils/requester';
import unauthorizedImg from '../../../images/unauthorized.png';
import Termin from './product/Termin';

export default class Catalog extends Component {
    constructor(props) {
        super(props);

        this.state = {
            termins: [],
        }
    }

    isAdmin = () => {
        if (sessionStorage.getItem('roleId') && sessionStorage.getItem('username') && sessionStorage.getItem('authtoken')) {
            return true;
        }
        return false;
    };

    isLogged = () => {
        if (sessionStorage.getItem('authtoken') && sessionStorage.getItem('userId') && sessionStorage.getItem('username')) {
            return true;
        }
        return false;
    };

    sortTermins = (termin1, termin2) => {
        if (termin1.terminDate > termin2.terminDate) {
            return 1;
        } else if (termin1.terminDate < termin2.terminDate) {
            return -1;
        }

        if (termin1.terminHour > termin2.terminHour) {
            return 1;
        } else if (termin1.terminHour < termin2.terminHour) {
            return -1;
        } else {
            return 0;
        }
    };

    getTermins = () => {
        requester.get('appdata', 'termins', 'kinvey')
            .then(res => {
                res.sort(this.sortTermins);

                this.setState({termins: res});
            });
    };

    componentDidMount = () => {
        this.getTermins();
    };

    render = () => {
        let unauthorized = (
            <section id="viewAds" className="viewAds">
                <h1 className="titleForm">Хитруваме, а?</h1>
                <div id="ads" className="ads">
                    <div className="noProductsPage">
                        <h2 className="body-text">Моля влезте в акаунта си или се регистрирайте, за да разгледате обявите!</h2>
                        <img src={unauthorizedImg} title="Нямате права!" alt="No products.." />
                    </div>
                </div>
            </section>
        );

        return (
            <main>
                <section id="viewAds" className="viewAds">
                    <h1 className="titleForm">Термини: {this.state.termins.length}</h1>
                    <div id="ads" className="ads">

                        <ul>
                            {
                                (!this.isLogged())
                                ? unauthorized
                                : (this.state.termins.map((t) => <Termin key={t._id} {...t} />))
                            }
                        </ul>

                    </div>
                </section>
            </main>
        )
    }
}


