import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import requester from '../../../../utils/requester';
import notificator from '../../../../utils/notificator';

export default class Termin extends Component {
    changeVisitState = () => {
        this.getTerminById(this.props._id)
            .then((data) => {
                if (data.isVisited === 'true') {
                    data.isVisited = 'false';
                } else {
                    data.isVisited = 'true';
                }
                requester.update('appdata', `termins/${this.props._id}`, 'kinvey', data)
                    .then((res) => {
                        notificator.showInfo('Успешно зададено състояние!');
                        localStorage.setItem('endpoint', '/catalog');
                    }).catch((res) => {
                    notificator.showError('Неуспешно задаване на състояние!');
                    localStorage.setItem('endpoint', '/catalog');
                });
            })
    };

    changeDocumentState = () => {
        this.getTerminById(this.props._id)
            .then((data) => {
                if (data.hasDocument === 'true') {
                    data.hasDocument = 'false';
                } else {
                    data.hasDocument = 'true';
                }
                requester.update('appdata', `termins/${this.props._id}`, 'kinvey', data)
                    .then((res) => {
                        notificator.showInfo('Успешно зададено състояние!');
                        localStorage.setItem('endpoint', '/catalog');
                    }).catch((res) => {
                        notificator.showError('Неуспешно задаване на състояние!');
                        localStorage.setItem('endpoint', '/catalog');
                    });
            })
    };

    haveDocument = () => {
        if (this.props.hasDocument === 'true') {
            return true;
        }
        return false;
    };

    hasBeenVisited = () => {
        if (this.props.isVisited === 'true') {
            return true;
        }
        return false;
    };

    getTerminById = (terminId) => {
        const ENDPOINT = `termins/${terminId}`;

        return requester.get('appdata', ENDPOINT, 'kinvey');
    };

    archiveTermin = (event) => {
        let terminId = event.target.getAttribute('data-terminid');

        this.getTerminById(terminId)
            .then((data) => {
                const ENDPOINT = `termins/${terminId}`;

                requester.remove('appdata', ENDPOINT, 'kinvey')
                    .then((response) => {
                        localStorage.setItem('endpoint', '/catalog');
                        localStorage.setItem('message', 'Изтрива се...');
                        data['deletedBy'] = sessionStorage.getItem('username');
                        data['deletedOn'] = Date.now();

                        requester.post('appdata', 'archived', 'kinvey', data).then(() => {
                            notificator.showInfo('Успешно изтрихте термина!');
                        });
                    })
            })
    };

    render = () => {
        let editEndpoint = `/edit/${this.props._id}`;
        let mapUrl = `https://www.google.com/maps/search/?api=1&query=Berlin+${this.props.street}+${this.props.postCode}`;

        const noDocAction = (
            <div className="productActions"><Link to="/loading"><button onClick={this.changeDocumentState} className="noDocumentLink">&#9783;</button></Link></div>
        );
        const docAction = (
            <div className="productActions"><Link to="/loading"><button onClick={this.changeDocumentState} className="documentLink">&#9776;</button></Link></div>
        );

        const noVisitAction = (
            <div className="productActions"><Link to="/loading"><button onClick={this.changeVisitState} className="noVisitLink">&#9872;</button></Link></div>
        );
        const visitAction = (
            <div className="productActions"><Link to="/loading"><button onClick={this.changeVisitState} className="visitLink">&#9873;</button></Link></div>
        );

        return (
            <li>
                <div className="productActions"><Link to={editEndpoint}><button className="editLink">&#9998;</button></Link></div>
                <div className="productActions"><Link to="/loading"><button className="deleteLink" onClick={this.archiveTermin} data-terminid={this.props._id}>&#10006;</button></Link></div>
                <Link to={"/details/" + this.props._id}>
                    <div className="productBrand">Дата: {this.props.terminDate}</div>
                    <div className="productModel">Час: {this.props.terminHour}</div>
                    <div className="productPicture"><img src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/9f/Facebook_Home_logo.svg/2000px-Facebook_Home_logo.svg.png" height="180" alt="Termin Logo" /></div>
                    <div className="productSize">{this.props.street}</div>
                    <div className="productSize">{this.props.postCode}</div>
                    <div className="productInfo">{this.props.company} | {this.props.rooms}</div>
                    <div className="productPrice">{this.props.warmRent}€</div>
                </Link>
                {this.haveDocument() ? docAction : noDocAction}
                {this.hasBeenVisited() ? visitAction : noVisitAction}
                <div className="productMapUrl"><a href={mapUrl} target="_blank">ВИЖ КАРТА</a></div>
            </li>
        )
    }
}


