import React, { Component } from 'react';
import requester from '../../../utils/requester';
import notificator from '../../../utils/notificator';

export default class DeleteByDate extends Component {
    constructor(props) {
        super(props);

        this.state = {
            results: [],
            searchDate: ''
        }
    }

    hasResults = () => {
        if (this.state.results.length === 0) {
            return false;
        }
        return true;
    };

    deleteTerminById = (terminId) => {
        const ENDPOINT = `termins/${terminId}`;

        return requester.remove('appdata', ENDPOINT, 'kinvey');
    };

    archiveTerminById = (data) => {
        const ENDPOINT = `archived`;

        return requester.post('appdata', ENDPOINT, 'kinvey', data)
    };

    isAdmin = () => {
        if (sessionStorage.getItem('roleId') && sessionStorage.getItem('username') && sessionStorage.getItem('authtoken')) {
            return true;
        }
        return false;
    };

    deleteTargetedTermins = () => {
        for (let i = 0; i < this.state.results.length; i++) {
            let currentTermin = this.state.results[i];
            let currentTerminId = currentTermin._id;

            currentTermin['deletedBy'] = sessionStorage.getItem('username');
            currentTermin['deletedOn'] = Date.now();

            this.archiveTerminById(currentTermin).then(() => {
                this.deleteTerminById(currentTerminId);
            });
        }
        this.props.history.push('/loading');
        localStorage.setItem('endpoint', '/catalog');
        notificator.showInfo('Успешно изтрихте всички термини на дата: ' + this.state.searchDate);
    };

    componentDidMount = () => {

    };

    handleChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;

        this.setState({
            [fieldName]: fieldValue
        });
    };

    handleSubmit = (event) => {
        event.preventDefault();

        //Validation:
        if (this.state.searchDate === '') {
            notificator.showError('Моля попълнете полето за търсене по дата!');
            return;
        }
        //End of validation!

        let data = {
            terminDate: this.state.searchDate
        };

        //Now check for termins in db by date.
        requester.get('appdata', `termins?query={"terminDate":"${data.terminDate}"}`, 'kinvey')
            .then(res => {
                if (res.length === 0) {
                    notificator.showError('Няма намерени резултати!');
                }
                this.setState({results: res});
            });
    };

    render = () => {
        const submitDeleting = (
            <div className="submitOrderForm">
                <h3 className="deleteTargetedInfo body-text">Термини за дата: {this.state.searchDate}</h3>
                <h4 className="deleteTargetedInfo">{this.state.results.length}</h4>
                <div className="deleteTargetedInfo">Потвърди изтриването:</div>
                <div><button className="btnDeleteTargeted" onClick={this.deleteTargetedTermins}>Потвърждавам!</button></div>
            </div>
        );

        return (
            <main>
                <section id="viewAds" className="viewAds">
                    <h1 className="titleForm">Изтриване на термини по дата</h1>
                    <div id="ads" className="ads">
                        <form id="formCreateAd" className="form" onSubmit={this.handleSubmit}>
                            <h1 className="titleForm">Търси термини по дата</h1>
                            <div>Дата на термин:</div>
                            <div><input type="text" onChange={this.handleChange} value={this.state.searchDate} className="inputModel" name="searchDate" /></div>

                            <div><input type="submit" value="Търси!" /></div>
                        </form>
                        {
                            (!this.hasResults())
                                ? null
                                : submitDeleting
                        }
                    </div>
                </section>
            </main>
        )
    }
}


