import React, {Component} from 'react';
import requester from "../../../utils/requester";
import notificator from "../../../utils/notificator";
import unauthorizedImg from '../../../images/unauthorized.png';

export default class CreateAdForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            terminDate: '',
            terminHour: '',
            street: '',
            postCode: '',
            company: '',
            warmRent: '',
            rooms: '',
            addedBy: sessionStorage.getItem('username')
        }
    }

    isAdmin = () => {
        if (sessionStorage.getItem('roleId') && sessionStorage.getItem('username') && sessionStorage.getItem('authtoken')) {
            return true;
        }
        return false;
    };

    handleChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;

        this.setState({
            [fieldName]: fieldValue
        });
    };

    handleSubmit = (event) => {
        event.preventDefault();

        //Validation:
        if (this.state.terminDate === '') {
            notificator.showError('Моля попълнете полето за дата!');
            return;
        } else if (this.state.terminHour === '') {
            notificator.showError('Моля попълнете полето за час!');
            return;
        } else if (this.state.street === '') {
            notificator.showError('Моля попълнете полето за адрес!');
            return;
        } else if (this.state.postCode === '') {
            notificator.showError('Моля попълнете полето за пощенски код!');
            return;
        } else if (this.state.company === '') {
            notificator.showError('Моля попълнете полето за фирма!');
            return;
        } else if (this.state.warmRent === '') {
            notificator.showError('Моля попълнете полето за топъл наем!');
            return;
        } else if (this.state.rooms === '') {
            notificator.showError('Моля попълнете полето за брой стаи!');
            return;
        }
        //End of validation!

        let data = {
            terminDate: this.state.terminDate,
            terminHour: this.state.terminHour,
            street: this.state.street,
            postCode: this.state.postCode,
            company: this.state.company,
            warmRent: this.state.warmRent,
            rooms: this.state.rooms,
            addedBy: this.state.addedBy,
            createdOn: Date.now()
        };
        //Now submit new ad to DB.
        requester.post('appdata', 'termins', 'kinvey', data)
            .then((res) => {
                notificator.showInfo('Успешно създаден термин!');
                this.props.history.push('/catalog');
            }).catch((res) => {
                notificator.showError(res.responseJSON.description);
            });
    };

    render = () => {
        let unauthorized = (
            <section id="viewAds" className="viewAds">
                <h1 className="titleForm">Хитруваме, а?</h1>
                <div id="ads" className="ads">
                    <div className="noProductsPage">
                        <h2>Нямате права, за да видите тази страница!</h2>
                        <img src={unauthorizedImg} title="Нямате права!" alt="No products.." />
                    </div>
                </div>
            </section>
        );

        let authorized = (
            <section id="viewCreateAd" className="viewCreateAd">
                <h1 className="titleForm">Създай нов термин</h1>
                <form id="formCreateAd" className="form" onSubmit={this.handleSubmit}>
                    <div>Дата на термин:</div>
                    <div><input type="text" onChange={this.handleChange} value={this.state.terminDate} className="inputModel" name="terminDate" /></div>
                    <div>Час на термин:</div>
                    <div><input type="text" onChange={this.handleChange} value={this.state.terminHour} className="inputModel" name="terminHour" /></div>
                    <div>Адрес:</div>
                    <div><input type="text" onChange={this.handleChange} value={this.state.street} className="inputModel" name="street" /></div>
                    <div>Пощенски код:</div>
                    <div><input type="text" onChange={this.handleChange} value={this.state.postCode} className="inputModel" name="postCode" /></div>
                    <div>Фирма:</div>
                    <div><input type="text" onChange={this.handleChange} value={this.state.company} className="inputModel" name="company" /></div>
                    <div>Топъл наем:</div>
                    <div><input type="text" onChange={this.handleChange} value={this.state.warmRent} className="inputModel" name="warmRent" /></div>
                    <div>Брой стаи:</div>
                    <div><input type="text" onChange={this.handleChange} value={this.state.rooms} className="inputModel" name="rooms" /></div>
                    <div>Добавил:</div>
                    <div><input type="text" disabled onChange={this.handleChange} value={this.state.addedBy} className="inputModel" name="addedBy" /></div>
                    <div><input type="submit" id="buttonCreateAd" value="Създай!" /></div>
                </form>
            </section>
        );

        return (
            <main>
                {this.isAdmin() ? authorized : unauthorized}
            </main>
        )
    }
}


