import React, { Component } from 'react';
import requester from '../../../utils/requester';
import Termin from './product/Termin';
import notificator from '../../../utils/notificator';

export default class Search extends Component {
    constructor(props) {
        super(props);

        this.state = {
            searchDate: '',
            filteredResults: []
        }
    }

    isLogged = () => {
        if (sessionStorage.getItem('authtoken') && sessionStorage.getItem('userId') && sessionStorage.getItem('username')) {
            return true;
        }
        return false;
    };

    getTermins = () => {
        requester.get('appdata', 'termins', 'kinvey')
            .then(res => {
                this.setState({termins: res});
            });
    };

    componentDidMount = () => {

    };

    handleChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;

        this.setState({
            [fieldName]: fieldValue
        });
    };

    handleSubmit = (event) => {
        event.preventDefault();

        //Validation:
        if (this.state.searchDate === '') {
            notificator.showError('Моля попълнете полето за търсене по дата!');
            return;
        }
        //End of validation!

        let data = {
            terminDate: this.state.searchDate
        };

        //Now check for termins in db by date.
        requester.get('appdata', `termins?query={"terminDate":"${data.terminDate}"}`, 'kinvey')
            .then(res => {
                res.sort(sortArr);

                function sortArr(termin1, termin2) {
                    if (termin1.terminDate > termin2.terminDate) {
                        return 1;
                    } else if (termin1.terminDate < termin2.terminDate) {
                        return -1;
                    }

                    if (termin1.terminHour > termin2.terminHour) {
                        return 1;
                    } else if (termin1.terminHour < termin2.terminHour) {
                        return -1;
                    } else {
                        return 0;
                    }
                }
                this.setState({filteredResults: res});
            });
    };

    hasResults = () => {
        if (this.state.filteredResults.length === 0) {
            return false;
        }
        return true;
    };

    render = () => {
        return (
            <main>
                <section id="viewAds" className="viewAds">
                    <h1 className="titleForm">Термини: {this.hasResults() ? (this.state.filteredResults.length) : null}</h1>
                    <div id="ads" className="ads">
                        <form id="formCreateAd" className="form" onSubmit={this.handleSubmit}>
                            <h1 className="titleForm">Търси термин по дата</h1>
                            <div>Дата на термин:</div>
                            <div><input type="text" onChange={this.handleChange} value={this.state.searchDate} className="inputModel" name="searchDate" /></div>

                            <div><input type="submit" id="buttonCreateAd" value="Търси!" /></div>
                        </form>

                        {(!this.hasResults())
                        ? null
                        : (<h1 className="titleForm searchResultsHeading">Резултати:</h1>)}
                        <ul>
                            {
                                (!this.hasResults())
                                    ? null
                                    : (this.state.filteredResults.map((t) => <Termin key={t._id} {...t} />))
                            }
                        </ul>

                    </div>
                </section>
            </main>
        )
    }
}


